.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}
.page__main {
  flex: 1 1 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.clear-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
}
html {
  width: 100%;
}
body {
  height: 100%;
  font-family: "Lato", "Times New Roman", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  margin: 0;
}
.primary-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  box-sizing: border-box;
  padding: 5px 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
}
.primary-btn:hover {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
}
.primary-btn:focus {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
  outline: none;
}
.primary-btn:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(94, 170, 47, 0.3);
}
.secondary-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 0;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  border: none;
  outline: none;
  cursor: pointer;
}
.secondary-btn:hover {
  color: rgba(0, 0, 0, 0.6);
}
.secondary-btn:focus {
  color: rgba(0, 0, 0, 0.6);
  outline: none;
}
.secondary-btn:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .secondary-btn {
    font-size: 20px;
    line-height: 20px;
  }
}
.third-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-sizing: border-box;
  min-height: 40px;
  padding: 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  background-color: #f2f2f2;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
}
.third-btn:hover {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
}
.third-btn:focus {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
  outline: none;
}
.third-btn:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: rgba(235, 235, 235, 0.3);
}
.input-field {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  background-color: transparent;
  border: 1px solid #e7e7e7;
  outline: none;
  padding: 10px;
}
.input-field:hover:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #cdcdcd;
  outline: 1px solid #cdcdcd;
}
.input-field:focus:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #444444;
  outline: 1px solid #444444;
}
.input-field:focus + .configuration-group__icon {
  stroke: #68b738;
}
.input-field:active:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #444444;
  outline: 1px solid #444444;
}
.input-field:active + .configuration-group__icon {
  stroke: #68b738;
}
.input-field:focus:invalid {
  border: 1px solid #ff8282;
  outline: 1px solid #ff8282;
}
.input-field:focus:invalid + .configuration-group__icon {
  stroke: #ff8282;
}
.input-field::placeholder {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
}
@media (min-width: 768px) {
  .input-field {
    font-size: 20px;
    line-height: 20px;
  }
}
.connection {
  position: relative;
  margin: 0;
  padding: 0;
}
.connection__contact {
  position: relative;
}
.contact {
  padding: 30px 0 0 0;
  position: relative;
}
.contact__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 18px;
  min-height: 60px;
}
@media (min-width: 768px) {
  .contact__container {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .contact__container {
    max-width: 1220px;
  }
}
.contact__title {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #111111;
  text-transform: uppercase;
}
.contact__text {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #444444;
}
.contact__text > span {
  display: block;
}
.contact--catalog {
  background-color: #f2f2f2;
}
.contact--form {
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .contact__container > p {
    padding: 60px 40px 40px 40px;
    margin: 0;
  }
  .contact__title {
    font-size: 20px;
    line-height: 26px;
  }
  .contact__text {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }
  .contact__text > span {
    margin-top: 20px;
  }
}
@media (min-width: 1440px) {
  .contact {
    width: 1220px;
    margin: 0 auto;
    padding: 0;
    z-index: 2;
  }
  .contact__container {
    content: "";
    position: absolute;
    top: 95px;
    left: 0;
    width: 565px;
    min-height: 199px;
    padding: 20px 30px 20px 30px;
    background-color: #ffffff;
    z-index: 2;
  }
  .contact__container > p {
    padding: 60px 50px 30px 30px;
    text-align: left;
  }
  .contact--catalog {
    background-color: transparent;
  }
  .contact--form {
    background-color: transparent;
  }
}
.map {
  position: relative;
}
.map__spb {
  display: block;
  width: 100%;
  min-height: 362px;
  border: none;
  position: relative;
  z-index: 1;
}
.map__spb::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/pictures/logo_pin.png");
  width: 57px;
  height: 53px;
  z-index: 1;
}
@media (min-width: 768px) {
  .map__spb::after {
    width: 113px;
    height: 106px;
  }
}
.footer {
  position: relative;
  background-color: #f2f2f2;
}
.footer__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
  display: grid;
  place-items: center;
  row-gap: 18px;
  min-height: auto;
  padding: 50px 20px;
}
@media (min-width: 768px) {
  .footer__container {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .footer__container {
    max-width: 1220px;
  }
}
.footer__logo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer__social {
  display: flex;
  min-width: 132px;
  min-height: 22px;
}
.footer__partner {
  display: flex;
  min-height: 34px;
}
.footer__line {
  width: 100%;
  border: none;
  outline: none;
  height: 1px;
  background-color: #d9d9d9;
  margin: 0;
}
@media (min-width: 768px) {
  .footer__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: 147px;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .footer__partner {
    width: 168px;
  }
  .footer__line {
    display: none;
  }
}
@media (min-width: 1440px) {
  .footer__container {
    min-height: 169px;
  }
  .footer__line {
    display: none;
  }
}
.social__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-width: 132px;
  column-gap: 10px;
  margin: 0;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  box-sizing: border-box;
  background-position: center;
  fill: #666666;
  border: none;
  outline: none;
  cursor: pointer;
}
.social__icon {
  display: block;
  width: 28px;
  height: 22px;
}
.social__link:hover {
  fill: #68b738;
}
.social__link:focus {
  fill: #68b738;
}
.social__link:active {
  fill: rgba(104, 183, 56, 0.3);
}
.partner__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-width: 280px;
  min-height: 34px;
  margin: 0;
  padding: 0;
}
.partner__link {
  background-position: center;
  text-decoration: none;
  border: none;
  outline: none;
  cursor: pointer;
}
.partner__link--text {
  font-family: "Lato", "Times New Roman", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #444444;
}
.partner__link--text:hover {
  color: rgba(68, 68, 68, 0.6);
}
.partner__link--text:focus {
  color: rgba(68, 68, 68, 0.6);
}
.partner__link--text:active {
  color: rgba(68, 68, 68, 0.3);
}
.partner__icon {
  display: block;
  width: 27px;
  height: 34px;
  border: none;
  fill: #666666;
}
.partner__icon:hover {
  fill: #68b738;
}
.partner__icon:focus {
  fill: #68b738;
}
.partner__icon:active {
  fill: rgba(104, 183, 56, 0.3);
}
@media (min-width: 768px) {
  .partner__list {
    min-width: 168px;
  }
}
